import Ajax from './utils/ajax/index.js'
import {login, getUserInfoData} from './utils/login.js'
import config from '../assets/js/config.js'
import logo from './components/logo.vue'
import userAgree from './components/userAgree.vue'
export default {
	components: {
		logo,
		userAgree
	},
	data() {
		return {
			msmVal: '',
			phoneVal: '',
			phoneValNum: '',
			msmVallength: false,
			disabled: false,
			timerText: '获取验证码',
		}
	},
	onLoad(options) {
		this.phoneVal = options.phoneVal
	},
	methods: {
		bindinput(event) {
			let value = event.detail.value;
			this.msmVal = value;
			if (value.length == 6) {
				this.msmVallength = true
			} else {
				this.msmVallength = false
			}
		},
		isTelphone(phone) {
			const reg = /^1\d{10}/;
			return reg.test(phone);
		},
		sendCode() {
			this.getCodeAjax()
		},
		//倒计时
		countdown() {
			const that = this;
			let times = 60;
			const timer = setInterval(function() {
				times--;
				if (times <= 0) {
					that.disabled = false
					that.timerText = '获取验证码'
					clearInterval(timer);
				} else {
					that.disabled = true
					that.timerText = '重新获取' + '(' + times + ')'
				}
			}, 1000);
		},
		// 注册的手机号码拿去短信验证码
		getCodeAjax() {
			let phoneVal = this.phoneVal
			let phoneValData = phoneVal.replace(/\s*/g, "");
			Ajax({
				url: `${config.getMsmCode}/${phoneValData}`, // 已经注册的号码 发验证码
				method: 'post',
				// contentType: 'X-Requested-With',
				contentType: 'form',
				returnAll: true,
				data: {
					terminalType: 'WEAPP'
				}
			}).then((res) => {
				if (res.data.success) {
					this.common.showToast('验证码发送成功')
					this.countdown();
				} else {
					this.common.showToast(res.data.exceptionMessage)
				}
			}).catch((err) => {
				console.log('err' + JSON.stringify(err));
			});
		},
		// 是否输入验证码
		loginReager() {
			if (!this.msmVal) {
				this.common.showToast('请输入验证码！')
				return;
			}else{
				//手机号码已注册登录 
				let phoneData = this.phoneVal.replace(/\s*/g, '')
				let data = {
						password: this.msmVal,
						username: phoneData,
						terminalType: 'WEAPP',
						loginType: 'sms',
						rememberMe: 'true',
						reLogin: true
				}
				login(data,'phone')
			}
		}
	}
}
